import React from "react";
import "./tiles.scss"

export default class Tiles extends React.Component<{
  images: string[]
}> {
  
  render() {

    return (
      <div className='tiles'>
        {(this.props.images.map(image => {
          return (
            <div key={image} className='tile' style={{ backgroundImage: `url(${image})` }}></div>
          );
        }))}
      </div>
    );
  }

};



